<script>
// import indexHeader from "@/components/header.vue";
import { showToast } from 'vant';
import indexTitle from "@/components/title.vue";
import item from "@/components/item.vue";
import foot from "@/components/foot.vue";
import {getData} from '../api/index'
export default {
  components: {
    // indexHeader,
    indexTitle,
    item,
    foot,
  },
  data() {
    return {
      showCode:true,
      time:0,
      phone:'',
      code: '',
      checked: false,
      show:false
    };
  },
  methods: {
    getCode(){
      if(!/^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(this.phone)){
        console.log(999)
        return showToast('')
      }
      this.showCode = false;
      this.time = 5*1000;
     
      const param = {
        action:'getclubcode',
        phone:this.phone
      }
      getData(param).then(res => {
        if(res.code == 0){
          showToast('')
        } else {
          showToast(res.msg)
        }
      })
    },
    timeFinish(){
      this.showCode = true;
    },
    submit(){
      if(!/^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(this.phone)){
        return showToast('')
      } 
      if(!this.code.trim().length){
        return showToast('')
      }
      const param = {
        action:'userclublogin',
        phone:this.phone,
        auth_code:this.code
      }
      getData(param).then(res => {
        if(res.code == 0){
          showToast('')
          setTimeout(() => {
            this.$router.push({
              name:'register'
            })
          },1500)
        } else {
          showToast(res.msg)
        }
      })
      
    }
  },
};
</script>

